    import {UserModel} from "../../model/user";
    import noData from "../../components/noData/noData"
    const  userModel = new UserModel()
export default {
  data() {
    return {
        list:[],
        pages:{
            page:1,
            size:10
        }
    };
  },
  methods: {
      _getBalance(){
          let data = this.pages
          userModel.getBalances(data).then((res)=>{
            if(res.code < 400) {
                this.list = res.data
            }
          })
      },
  },
  components: {noData},
  onLoad: function() {
      this._getBalance()
  },
  onShow: function() {},
  onHide: function() {}
};
